:root {
  --vdarkpink: #d31342;
  --pinkbtnhover: #c7133e;
  --greybtnhover: #ecebeb;
  --buttonhome: #d31342;
  --pinkicon: #fea5bb;
  --darkpink: #d41443;
  --pink: #ffa5ba;
  --lightpink: #ffb8ca;
  --modalpink: #f4e6e9;
  --backgroundpink: #fff5f6;
  --white: #fff;
  --lgrey: #fafafa;
  --grey: #d9d9d9;
  --textgrey: #907e82;
  --black: #000;
  --beige: #f0d1a5;
  --mint: #c2e297;
  --modalmenucolor: #e17992;
  --footerborder: #e1e1e1;
  --desktop: 1200px;
  --tablet: 768px;
  --mobile: 320px;
  --cubic-bezier: cubic-bezier(.4, 0, .2, 1);
  --titan: "Titan One";
  --shadow: 0px 8px 30px #d414431a;
}

body {
  color: var(--black);
  font-family: DM Sans, sans-serif;
  font-weight: 400;
}

li, ul {
  list-style: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.logo {
  width: 199px;
  height: 50px;
  margin-top: 6px;
}

.header-box {
  background-color: var(--lightpink);
  background-image: url("bigice.27cbed36.png"), url("ellipse.a67bac4b.png");
  background-position: 117px 94px, 36px 83px;
  background-repeat: no-repeat;
  position: relative;
}

.header-box__text {
  max-width: 218px;
  color: var(--white);
  font-style: normal;
  font-weight: 400;
  font-family: var(--titan);
  letter-spacing: .04em;
  text-transform: uppercase;
  margin-top: 137px;
  font-size: 26px;
  line-height: 1.15;
}

.header-box__text--item {
  color: var(--darkpink);
  font-size: 48px;
  line-height: .96;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .header-box {
    background-color: var(--lightpink);
    background-image: url("bigice.27cbed36.png"), url("ellipse.a67bac4b.png");
    background-position: 117px 94px, 36px 83px;
    background-repeat: no-repeat;
    position: relative;
  }
}

.btn-box {
  text-align: center;
  align-items: center;
  margin-top: 19px;
  padding-bottom: 160px;
  display: flex;
}

.btn-box__red, .btn-box__white {
  height: 24px;
  background-color: var(--darkpink);
  color: var(--white);
  border: 0;
  border-radius: 50px;
  margin-right: 8px;
  padding-left: 15.5px;
  padding-right: 15.5px;
  font-size: 8px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.25;
}

.btn-box__white {
  background-color: var(--white);
  color: var(--darkpink);
}

.about {
  background-color: var(--backgroundpink);
  background-image: url("cowmilkabout.4594f3e9.png");
  background-position: 50% 80%;
  background-repeat: no-repeat;
  background-size: contain;
  padding-top: 125px;
  padding-bottom: 39px;
}

@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .about {
    background-image: url("cowmilkabout@2x.7ca042bc.png");
  }
}

.about__cow {
  width: 280px;
  height: 279px;
  background-image: url("cowmilk.b80e89f8.png");
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  margin-bottom: 46px;
  margin-left: 0;
  margin-right: 0;
  display: flex;
}

@media (min-device-pixel-ratio: 2), (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .about__cow {
    background-image: url("cowmilk@2x.7b128d4f.png");
  }
}

.about__container {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.about__content {
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 30px;
  display: flex;
}

.about__header {
  font-family: var(--titan);
  text-transform: uppercase;
  text-align: center;
  font-weight: 400;
}

.about__header--label {
  color: var(--pink);
  margin-bottom: 15px;
  font-size: 18px;
  line-height: 21px;
}

.about__header--title {
  letter-spacing: .04em;
  color: var(--darkpink);
  margin-bottom: 26px;
  font-size: 34px;
  line-height: 39px;
}

.about__description {
  margin-bottom: 35px;
  font-size: 12px;
  font-weight: 500;
  line-height: 162%;
}

.about__description--primary {
  color: var(--black);
}

.about__description--secondary {
  color: var(--textgrey);
}

.button--about {
  background-color: var(--white);
  color: var(--darkpink);
  transition-property: background-color;
  transition-duration: .3s;
  transition-timing-function: var(--cubic-bezier);
  height: 44px;
  width: 176px;
  border: 0;
  border-radius: 22px;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  margin: 0;
  padding: 0 37px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3;
  display: flex;
}

.button--about:after {
  content: "";
  width: 8px;
  height: 10px;
}

.button--about:hover, .button--about:focus {
  transition: fill .25s var(--cubic-bezier);
  cursor: pointer;
  background-color: var(--greybtnhover);
}

