.customer-review {
  margin: 0 auto;
  padding: 80px 20px;
  font-family: DM Sans, sans-serif;
  font-weight: 400;
}

.customer-review__list {
  padding-inline-start: 0;
  list-style: none;
}

.customer-review__item {
  flex-direction: column;
  align-items: center;
  gap: 30px;
  display: none;
}

.customer-review__item.active {
  display: flex;
}

.customer-review__text-container {
  text-align: center;
  background-color: #fafafa;
  padding: 40px 42px 25px;
  position: relative;
}

.customer-review__text-container:after {
  content: "";
  width: 32px;
  height: 4px;
  background-image: url("review-dots.9ce4972f.png");
  position: absolute;
  bottom: -25px;
  left: 50%;
  right: 3px;
  transform: translateX(-50%);
}

.customer-review__text {
  z-index: 2;
  text-align: center;
  background: repeating-linear-gradient(#0000, #0000 29px, #e1e1e1 29px 30px);
  flex-direction: column;
  margin-block: 0;
  padding: 2px 25px 0 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 2.55;
  position: relative;
}

.customer-review__text:before {
  content: "";
  z-index: -1;
  width: 54px;
  height: 40px;
  background-image: url("review-quote.e1c954c9.png");
  position: absolute;
  top: -10px;
  left: 15px;
}

.customer-review__person {
  flex-direction: column;
}

.customer-review__person-name {
  padding-top: 18px;
  font-size: 14px;
}

.customer-review__slide-dots {
  argin-block-start: 0em;
  padding-inline-start: 0;
  justify-content: center;
  gap: 18px;
  margin-block-end: 0;
  padding-top: 30px;
  list-style: none;
  display: flex;
}

.customer-review__slide-dots li {
  width: 12px;
  height: 12px;
  cursor: pointer;
  background-color: #d9d9d9;
  border-radius: 50%;
}

.customer-review__slide-dots li svg {
  display: none;
}

.customer-review__slide-dots li.is-active svg {
  display: block;
}

.customer-review__icon {
  width: 18px;
  height: 18px;
  fill: --buttonhome;
  background-color: #fff;
  padding-bottom: 6px;
}

